.section {
  display: flex;
  position: relative;
  overflow: hidden;
  border-radius: 0.25rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  /* border: 1px solid red; */
}
.menulist-container {
  display: flex;
  width: 100%;
  height: 100%;
  padding: 0.125rem;
  background-color: #fff;
  overflow: auto;
}

.menulist-center {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: minmax(230px, 250px);
  width: 100%;
  /* height: 100%; */
  /* height: auto; */
  height: min-content;
  /* height: max-content; */
  border: 1px solid black;
  /* overflow: auto; */
}

@media screen and (max-width: 479px) {
  .section {
    /* margin: 0.25rem 0.4rem; */
    margin: 0 0.4rem;
    margin-bottom: 0.25rem;
    /* border: 1px solid black; */
  }
  .menulist-center {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(210px, 230px);
    gap: 0.15rem;
    padding: 0;
    gap: 0.1rem;
    /* border: 1px solid black; */
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  .section {
    margin: 0 0.5rem;
    margin-bottom: 0.25rem;
  }
  .menulist-center {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(230px, 250px);
    gap: 0.175rem;
    padding: 0;
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .section {
    /* border: 1px solid blue; */
    padding: 0 0.25rem;
    padding: 0.1rem 0.25rem;
    /* border: 1px solid red; */
  }
  .menulist-container {
    margin-right: 0.25rem;

    /* border: 1px solid red; */
  }
  .menulist-center {
    grid-template-columns: repeat(3, 1fr);
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(230px, 280px);
    gap: 0.2rem;
  }
}

@media screen and (min-width: 1024px) and (max-width: 1279px) {
  .section {
    padding: 0.1rem 0.25rem;

    /* border: 1px solid red; */
  }
  .menulist-container {
    margin-right: 0.3rem;
  }
  .menulist-center {
    grid-template-columns: repeat(4, 1fr);
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(250px, 280px);
    gap: 0.225rem;
  }
}
@media screen and (min-width: 1280px) and (max-width: 1439px) {
  .section {
    padding: 0.1rem 0.25rem;
    border: 1px solid blue;
  }
  .menulist-container {
    margin-right: 0.35rem;
    border: 1px solid blue;
  }
  .menulist-center {
    grid-template-columns: repeat(5, 1fr);
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(280px, 310px);
    gap: 0.25rem;
  }
}
@media screen and (min-width: 1440px) {
  .section {
    padding: 0.1rem 0.25rem;
    border: 1px solid red;
  }
  .menulist-container {
    margin-right: 0.4rem;
    border: 1px solid red;
  }
  .menulist-center {
    grid-template-columns: repeat(5, 1fr);
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(310px, 340px);
    gap: 0.275rem;
  }
}
